.description {
  position: absolute;
  z-index: 20;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  padding: 40px 20px 10px;
  height: 100vh; }
  .description h1 {
    margin: 0;
    margin-bottom: 20px;
    font-family: "Big Shoulders Display", "Arial", sans-serif;
    font-size: 90px;
    line-height: 100px;
    letter-spacing: 0.02em;
    color: #ffffff; }
  @media (max-width: 1023px) {
    .description {
      width: 35%; } }
  @media (max-width: 767px) {
    .description {
      flex-direction: row;
      padding-top: 70px;
      width: 100%;
      height: auto; }
      .description h1 {
        font-size: 70px;
        line-height: 80px; } }

.description__button {
  display: block;
  border: none;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  position: absolute;
  right: 9px;
  bottom: 90px;
  display: none;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #ffffff;
  font-size: 14px;
  line-height: 22px;
  font-weight: 700;
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }
  @media (max-width: 767px) {
    .description__button {
      display: block; } }

.description__info {
  margin-top: auto; }
  @media (max-width: 767px) {
    .description__info {
      position: absolute;
      top: 120px;
      left: 50%;
      display: none;
      flex-direction: column;
      opacity: 0;
      padding: 40px 15px 25px;
      width: 280px;
      border-radius: 10px;
      background-color: rgba(14, 14, 14, 0.8);
      backdrop-filter: blur(5px);
      transform: translateX(-50%);
      animation: fadeInFromNone 0.3s ease-in; }
      .description__info.active {
        display: flex;
        opacity: 1; } }

.description__text {
  color: #ffffff;
  text-align: center; }
  .description__text strong {
    display: none;
    margin-bottom: 20px;
    font-size: 20px; }
    @media (max-width: 767px) {
      .description__text strong {
        display: block; } }
  .description__text p {
    margin: 0;
    margin-bottom: 40px;
    font-size: 14px;
    max-width: 240px;
    text-align: left; }
    @media (max-width: 767px) {
      .description__text p {
        margin-bottom: 70px;
        max-width: 100%;
        font-size: 16px;
        text-align: center; } }

.description__credits {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 20px; }
  .description__credits a {
    transition: opacity 0.3s ease-out;
    color: #ffffff; }
    @media (min-width: 1024px) {
      .description__credits a:not(:last-child):hover {
        opacity: 0.7; } }
  @media (max-width: 767px) {
    .description__credits {
      align-items: flex-start; } }

.description__author {
  opacity: 0.2;
  font-size: 10px;
  line-height: 18px; }
  @media (max-width: 1023px) {
    .description__author {
      font-size: 12px; } }
  @media (max-width: 1023px) {
    .description__author {
      font-size: 10px; } }

.orbit {
  position: absolute;
  z-index: 50;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation-name: rotate-orbit;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform: translate(-50%, -50%);
  perspective: 1000px;
  transform-style: preserve-3d; }
  .orbit--mercury {
    width: 80px;
    height: 80px;
    animation-duration: 0.8797s; }
    .orbit--mercury .orbit__inner {
      transform: rotate(324deg); }
  .orbit--venus {
    width: 120px;
    height: 120px;
    animation-duration: 2.247s; }
    .orbit--venus .orbit__inner {
      transform: rotate(153deg); }
  .orbit--earth {
    width: 170px;
    height: 170px;
    animation-duration: 3.652s; }
    .orbit--earth .orbit__inner {
      transform: rotate(93deg); }
  .orbit--mars {
    width: 240px;
